@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.file-card {
  position: relative;
  grid-template-columns: 200px 220px 300px 140px 40px 120px 140px;
  margin-top: 8px;
  background-color: color.adjust(pick-color('primary', 'blue'), $lightness: 50%);
  border-radius: 5px;

  &::before {
    content: '';
    position: absolute;
    height: 100%;
    border-left: 4px solid pick-color('primary', 'web');
    width: 20px;
    border-radius: inherit;
  }

  .fc-filename {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .fc-cal {
    svg {
      fill: pick-color('primary', 'web');
    }
  }

  .fc-timing {
    .timing-duration,
    .timing-to {
      color: pick-color('primary', 'ash');
    }
  }

  .fc-location {
    cursor: auto;

    svg {
      width: 22px;
      height: 22px;
      fill: pick-color('primary', 'charcoal');
      flex-shrink: 0;
    }

    .location-label {
      margin-left: 4px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .fc-info {
    align-self: flex-end;
    text-align: end;

    span {
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &.file-not-exists {
    background-color: pick-color('primary', 'fog');
    cursor: not-allowed;

    &::before {
      border-left: 4px solid pick-color('primary', 'ash');
    }

    span {
      cursor: not-allowed;
      color: pick-color('primary', 'ash');
    }

    .file-type-icon {
      svg {
        fill: color.adjust(pick-color('primary', 'ash'), $lightness: 10%) !important;

        path,
        rect {
          fill: pick-color('primary', 'ash') !important;
        }
      }
    }

    .fc-cal svg,
    .fc-location svg {
      fill: pick-color('primary', 'ash');
    }
  }

  &.redaction-progress {
    background-color: color.adjust(pick-color('common', 'warning'), $lightness: 30%);
    cursor: not-allowed;

    &::before {
      border-left: 4px solid pick-color('common', 'warning');
    }

    span {
      color: pick-color('common', 'warning');
    }

    .fc-timing .timing-to,
    .fc-timing .timing-duration {
      color: pick-color('common', 'warning');
    }

    .fc-cal svg,
    .fc-location svg {
      fill: pick-color('common', 'warning');
    }
  }
}
